<template>
    <view class="body">
        <view class="wrapper">
            <form @submit.prevent="handleRegister">
                <h1 class="title">注册</h1>
                <view class="input-box">
                    <i class="fas fa-envelope icon"></i>
                    <input type="text" v-model="username" id="usernameInput" required ref="usernameInputRef"
                        @focus="moveLabel('username')" @blur="resetLabel('username')" />
                    <label :class="{ moved: isUsernameMoved }" for="usernameInput">Username</label>
                </view>
                <view class="input-box">
                    <i class="fas fa-lock icon"></i>
                    <input :type="passwordType" v-model="password" id="passwordInput" required ref="passwordInputRef"
                        @focus="moveLabel('password')" @blur="resetLabel('password')" />
                    <label :class="{ moved: isPasswordMoved }" for="passwordInput">Password</label>
                    <i class="fas fa-eye icon eye-icon" @click="togglePasswordVisibility('password')"></i>
                </view>
                <view class="input-box">
                    <i class="fas fa-lock icon"></i>
                    <input :type="confirmPasswordType" v-model="confirmPassword" id="confirmPasswordInput" required
                        ref="confirmPasswordInputRef" @focus="moveLabel('confirmPassword')"
                        @blur="resetLabel('confirmPassword')" />
                    <label :class="{ moved: isConfirmPasswordMoved }" for="confirmPasswordInput">Confirm
                        Password</label>
                    <i class="fas fa-eye icon eye-icon" @click="togglePasswordVisibility('confirmPassword')"></i>
                </view>
                <view class="input-box">
                    <i class="fas fa-user icon"></i>
                    <input type="text" v-model="title" id="titleInput" required ref="titleInputRef"
                        @focus="moveLabel('title')" @blur="resetLabel('title')" />
                    <label :class="{ moved: isTitleMoved }" for="titleInput">Title</label>
                </view>
                <view class="cell image-box preview-center">
                    <view v-if="bookImage" class="preview-image">
                        <image :src="bookImage" mode="aspectFill" style="width: 100px; height: 100px;" />
                    </view>
                </view>
                <button @click="chooseImage">选择图片</button>
                <button class="btn" @click="handleRegister">注册</button>
            </form>
        </view>
        <view class="signup-link-underline-out" @click="navigate">
            已有账号？立即登录
        </view>
    </view>
	
</template>

<script setup>
	import {
		ref
	} from 'vue';
	import {
		onMounted
	} from 'vue';
	import {
		useRouter
	} from 'vue-router';

	const username = ref('');
	const password = ref('');
	const confirmPassword = ref('');
	const email = ref('');
	const title = ref('');
	const bookImage = ref('');
	const isUsernameMoved = ref(false);
	const isPasswordMoved = ref(false);
	const isConfirmPasswordMoved = ref(false);
	const isEmailMoved = ref(false);
	const isTitleMoved = ref(false);
	const router = useRouter();
	const passwordType = ref('password');
	const confirmPasswordType = ref('password');

	async function chooseImage() {
		uni.chooseImage({
			count: 1, // 选择一张图片
			sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
			sourceType: ['album'], // 从相册选择
			success: function(res) {
				bookImage.value = res.tempFilePaths[0];
				console.log('Selected image:', bookImage.value); // 添加这行
			}
		});
	}

	function navigate() {
		uni.navigateTo({
			url: '/pages/login/login'
		})
	}

	function handleRegister() {
		if (password.value !== confirmPassword.value) {
			uni.showToast({
				title: '密码不一致',
				icon: 'none'
			});
			return;
		}

		const existingAccount = data.find(item => item.content.account === username.value);

		if (existingAccount) {
			uni.showToast({
				title: '用户名已存在',
				icon: 'none'
			});
			return;
		}

		// 将新用户数据添加到 accountData 中
		data.push({
			state: false,
			content: {
				account: username.value,
				password: password.value,
				img: bookImage.value ||
					'https://tse3-mm.cn.bing.net/th/id/OIP-C.0-vJS5KbFZwCSWe-dWqKrwHaHa?w=168&h=180&c=7&r=0&o=5&dpr=1.5&pid=1.7',
				title: title.value,
			}
		});

		uni.showToast({
			title: '注册成功',
			icon: 'success'
		});
		setTimeout(() => {
			uni.navigateTo({
				url: '/pages/login/login'
			});
		}, 2000); // 跳转到登录页面
	}

	function moveLabel(inputType) {
		if (inputType === 'username') {
			isUsernameMoved.value = true;
		} else if (inputType === 'password') {
			isPasswordMoved.value = true;
		} else if (inputType === 'confirmPassword') {
			isConfirmPasswordMoved.value = true;
		} else if (inputType === 'email') {
			isEmailMoved.value = true;
		} else if (inputType === 'title') {
			isTitleMoved.value = true;
		}
	}

	function resetLabel(inputType) {
		if (inputType === 'username' && !username.value) {
			isUsernameMoved.value = false;
		} else if (inputType === 'password' && !password.value) {
			isPasswordMoved.value = false;
		} else if (inputType === 'confirmPassword' && !confirmPassword.value) {
			isConfirmPasswordMoved.value = false;
		} else if (inputType === 'email' && !email.value) {
			isEmailMoved.value = false;
		} else if (inputType === 'title' && !title.value) {
			isTitleMoved.value = false;
		}
	}

	function togglePasswordVisibility(inputType) {
		if (inputType === 'password') {
			passwordType.value = passwordType.value === 'password' ? 'text' : 'password';
		} else if (inputType === 'confirmPassword') {
			confirmPasswordType.value = confirmPasswordType.value === 'password' ? 'text' : 'password';
		}
	}

	onMounted(() => {
		// 初始化时可以做一些操作，例如获取数据等
	});
</script>

<style scoped>
	.cell {
		display: flex;
		align-items: center;
		padding: 24rpx 32rpx;
		border-bottom: 1rpx solid #ebedf0;
	}

	.image-box {
		position: relative;
		display: flex;
		justify-content: center;
		margin-left: 250rpx;
		width: 120rpx;
		margin-top: 20rpx;
		margin-bottom: 20rpx;
		height: 150rpx;
		border: 1rpx solid #dcdfe6;
		border-radius: 8rpx;
		box-shadow: 0 4rpx 8rpx rgba(0, 0, 0, 0.05);
		overflow: hidden;
	}

	.title {
		color: #4e987a;
		font-weight: bolder;
		font-size: 60rpx;
		letter-spacing: 2px;
	}

	.body {
		width: 100vw;
		height: 100vh;
		display: flex;
		align-items: center;
		justify-content: center;
		background: linear-gradient(135deg, #e8f5e9 0%, #56a081 60%, #4e987a 100%);
	}

	.wrapper {
		position: relative;
		width: 340px;
		height: 520px;
		background: #fff;
		border-radius: 18px;
		box-shadow: 0 8px 32px 0 rgba(86,160,129,0.12);
		display: flex;
		flex-direction: column;
		align-items: center;
		padding: 40px 24px 32px 24px;
		overflow: hidden;
		left: 80rpx;
	}
	.signup-link-underline {
		position: absolute;
		bottom: 18px;
		right: 24px;
		color: #fff;
		font-size: 13px;
		text-decoration: underline;
		cursor: pointer;
		background: transparent;
		z-index: 2;
	}
	form {
		position: relative;
		width: 100%;
		height: 100%;
		box-sizing: border-box;
	}

	.wrapper h1 {
		font-size: 2em;
		color: #4e987a;
		text-align: center;
		margin-bottom: 24px;
	}

	.input-box {
		position: relative;
		width: 100%;
		margin: 18px 0 0 0;
		border-bottom: 2px solid #56a081;
	}

	.input-box label {
		position: absolute;
		top: 50%;
		left: 5px;
		transform: translateY(-50%);
		font-size: 1em;
		pointer-events: none;
		transition: 0.5s;
		color: #56a081;
	}

	.input-box input {
		width: 100%;
		height: 48px;
		background: transparent;
		border: none;
		outline: none;
		font-size: 1em;
		color: #222;
		padding: 0 40px 0 5px;
	}

	.input-box label.moved {
		top: -5px;
		font-size: 12px;
		color: #4e987a;
	}

	.input-box .icon {
		position: absolute;
		right: 8px;
		font-size: 1.2em;
		color: #56a081;
		line-height: 57px;
		cursor: pointer;
	}

	.input-box .eye-icon {
		right: 35px;
		cursor: pointer;
	}

	.cell.image-box {
		margin: 10px 0 0 0;
		border: none;
		box-shadow: none;
		background: none;
		padding: 0;
		justify-content: flex-start;
	}

	.preview-image image {
		border-radius: 8px;
		box-shadow: 0 2px 8px 0 rgba(86,160,129,0.10);
	}

	.wrapper .btn {
		width: 100%;
		height: 44px;
		background: linear-gradient(90deg, #56a081 0%, #4e987a 100%);
		outline: none;
		border: none;
		border-radius: 40px;
		cursor: pointer;
		font-size: 1em;
		font-weight: 600;
		color: #fff;
		margin-top: 24px;
		box-shadow: 0 2px 8px 0 rgba(86,160,129,0.10);
		transition: background 0.2s;
	}
	.btn:hover {
		background: linear-gradient(90deg, #4e987a 0%, #56a081 100%);
	}

	.wrapper .signup-link {
		font-size: 0.9em;
		color: #56a081;
		text-align: center;
		margin: 25px 0 10px;
	}
	.wrapper .signup-link a {
		color: #56a081;
		text-decoration: none;
		font-weight: 600;
		transition: color 0.2s;
	}
	.wrapper .signup-link a:hover {
		color: #4e987a;
		text-decoration: underline;
	}

	@media (max-width: 400px) {
		.wrapper {
			width: 96vw;
			max-width: 96vw;
			height: 520px;
			padding: 24px 8px 16px 8px;
		}
		.wrapper .input-box {
			width: 90vw;
		}
	}
	.preview-center {
		justify-content: center !important;
		align-items: center !important;
		display: flex !important;
	}
	.signup-link-underline-out {
		position: relative;
		top: 580rpx;
		left: -260rpx;
		color: #fff;
		font-size: 10px;
		text-decoration: underline;
		cursor: pointer;
		background: transparent;
		z-index: 2;
	}
</style>